<template>
  <div class="row">
    <div class="col-md-8 col-md-offset-2">
      <div class="card">
        <div class="header">
          <h4 class="title">Adding Oas</h4>
        </div>
        <div class="content">
          <form>
            <ImageUplodaer
              title="Add image"
              uploading-text="Uploading..."
              @uploadPoster="val => handleSetOa(val, 'image')"
              :poster="form.image"
            />
            <hr>
            <div class="row">
              <div class="col-md-12">
                <el-radio
                  v-model="type"
                  @change="handleChoose"
                  label="event"
                >
                  Event Oa
                </el-radio>
                <el-radio
                  v-model="type"
                  @change="handleChoose"
                  label="location"
                >
                  Location Oa
                </el-radio>
                <div class="row">
                  <br>
                </div>
                <div class="col-md-12 search_box row">
                  <SelectSearch
                    v-if="type"
                    :type="type"
                    name="parent_id"
                    :options="setFoundedItems"
                    :labelType="labelType"
                    :initialValue="form.initialValue"
                    @handleSearch="handleSearch"
                    @handleChange="handleSetOa"
                  />
                </div>
              </div>
            </div>
            <hr>
            <component
              type="text"
              v-for="({
                label,
                value,
                subValue,
                name,
                placeholder,
                showedKey,
                type,
                options,
                selected
              }, key) in fields"
              :selected="selected"
              :options="options"
              :is="type"
              :key="key"
              :label="label"
              :placeholder="placeholder"
              @changeValue="handleSetOa"
              :name="name"
              :value="form[value]"
            />
            <div class="text-center">
              <button 
                type="submit"
                class="btn btn-info btn-fill btn-wd" 
                @click.prevent="submitForm"
              >
                {{id ? 'Update' : 'Create'}}
              </button>
              <router-link 
                to="/oas/list" 
                class="btn btn-danger btn-fill btn-wd"
              >
                Back
              </router-link>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapState, mapMutations, mapGetters } from 'vuex'
import { types, fields } from './TableMock.js'

export default {
  data: () => ({
    id: '',
    types,
    fields,
    subedKey: 'location',
    type: ''
  }),
  components: {
    ImageUplodaer: () => import('ui/ImageUploaderPlugin/ImageUploader.vue')
  },
  mounted() {
    this.id = this.$route.params.id
    if (this.id) {
      this.fetchOa({ id: this.id })
    } else {
      this.clearOaForm()
    }
  },
  watch: {
    form() {
      this.type = this.form.type
    }
  },
  computed: {
    ...mapState({
      form: state => state.oas.oa
    }),
    ...mapGetters([
      'setFoundedItems'
    ]),
    labelType() {
      return 'title'
    }
  },
  methods: {
    submitForm () {
      const id = this.id || ''
      id ? this.updateOa({ id }) : this.createOa()
      this.$router.push('/oas/list')
    },
    ...mapActions({
      fetchOa: 'fetchOa',
      createOa: 'createOa',
      updateOa: 'updateOa',
      handleSearch: 'searchTypeItem'
    }),
    ...mapMutations({
      resetForm: 'resetOa',
      setPoster: 'setPoster',
      updateOaForm: 'updateOaForm',
      clearOaSearch: 'clearOaSearch',
      clearOaForm: 'clearOaForm'
    }),
    handleChoose(value) {
      this.clearOaSearch()
      this.$children[6].query = ''
      const payload = {
        value,
        key: 'type'
      }
      this.updateOaForm(payload)
    },
    handleSetOa(value, key) {
      const payload = {
        key,
        value
      }
      this.updateOaForm(payload)
    }
  }
}
</script>

<style>
  .search_box {
    padding-left: 0;
  }
</style>



